<template>
  <div>
    <el-row style="background: #193788;">
      <component :is="layoutHeader"></component>
    </el-row>
    <el-row>
      <el-col style="background: #f56c6c; width: 180px;position: fixed;" class="hidden-xs-only" >
        <component :is="layoutSide"></component>
      </el-col>
      <el-col style="background: #67c23a; margin-left: 180px;"  :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <router-view/>
      </el-col>
    </el-row>
    
    <el-row style="background: #999">
      <component :is="layoutFooter"></component>
    </el-row>
  </div>
</template>
<script>
export default {
  name: 'Layout',
  components: {
  },
  beforeRouteEnter: (to, from, next) => {
    var currentLayoutHeader
    var currentLayoutSide
    var currentLayoutFooter
    // 永远保留最底层的meta数据(Layout不允许嵌套使用)
    to.matched.forEach(record => {
      if (record.meta.layoutHeader) {
        currentLayoutHeader = record.meta.layoutHeader
      }
      if (record.meta.layoutSide) {
        currentLayoutSide = record.meta.layoutSide
      }
      if (record.meta.layoutSide) {
        currentLayoutFooter = record.meta.layoutFooter
      }
    })
    next(vm => {
      // 通过 `vm` 访问组件实例
      vm.setLayout(currentLayoutHeader, currentLayoutSide, currentLayoutFooter)
    })
  },
  props: {

  },
  data() {
    return {
      layoutHeader: '',
      layoutSide: '',
      layoutFooter: ''
    }
  },
  computed: {

  },
  watch: {

  },
  created() {

  },
  mounted() {

  },
  methods: {
    setLayout(layoutHeader, layoutSide, layoutFooter) {
      this.layoutHeader = layoutHeader
      this.layoutSide = layoutSide
      this.layoutFooter = layoutFooter
    }
  }
}
</script>



